.works {
    padding: 10rem 0;
    transition: all .3s ease;

    h2 {
        text-align: center;
    }

    .container {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        max-width: 100rem;
        width: 90%;
        margin: 0 auto;


    }

}

.item {
    position: relative;
    width: 20rem;
    height: 28rem;
    margin: 1rem;
    overflow: hidden;

    .img-item,
    .contenido-item {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

    }

    .img-item {
        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: .5rem;
        }

        &::before {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            display: block;
            background: darken($blanco, 15);
            opacity: .3;
            z-index: 1;
        }

    }

    .contenido-item::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $azul1;
        opacity: 0.95;
        transform: scaleX(0);
        transition: transform 0.3s cubic-bezier(0.69, 0.05, 0.34, 1);
        transform-origin: left;
        transition-delay: 0.3s;
        z-index: 2;
    }

    &:hover {
        .contenido-item::before {
            transform: scaleX(1);
            transition: transform 0.3s cubic-bezier(.694, .048, .335, 1);
            transform-origin: left;
            transition-delay: 0s;
        }
    }

    .contenido-item {
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 2;

        .cont {
            padding: 30px;
            z-index: 2;
            transition: .3s ease;
            transform: translateX(300px);
            transition-delay: 0s;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            text-align: center;
        }

        &:hover {
            .cont {
                transform: translateX(0);
                transition-delay: 0.3s;
            }
        }
    }

    .cont {
        h4 {
            font-size: 1.5rem;
            text-transform: uppercase;
            letter-spacing: 1px;
            color: $blanco;
            margin: .5rem;
        }

        p {
            font-size: 1.3rem;
            line-height: 1.4rem;
            letter-spacing: 1px;
            font-weight: 300;
            color: $blanco;
        }

        a {
            color: #b0b2c3;
            font-size: 1.6rem;
            margin: .5rem;

            &:hover {
                color: $celeste;
            }
        }
    }
}