.contacto {
    padding: 10rem 0;

    p {
        color: #c2c3d0;
        line-height: 1.5;

        a {
            color: $celeste;
            transition: all .3s ease;

            &:hover {
                text-shadow: 0 0 5px #00fff1, 0 0 20px #00fff1;
            }
        }
    }

    @include tablet {
        h2::after {
            content: "";
            display: block;
            position: relative;
            top: -2.5rem;
            right: -23rem;
            width: 20rem;
            height: 2px;
            background-color: $azul1;
            margin-left: 2rem;
        }
    }

    .form-contacto {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 4rem 1rem;
        flex-direction: column;
    }

}

.form {
    display: flex;
    justify-content: center;
    align-items: center;
}


.form ul {

    list-style: none;
    padding: 0;

    @include tablet {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 1rem;
        margin: 0;
        padding: 2rem 0 4rem 0;

        .li-asunto,
        .li-mensaje {
            grid-column: 1 / 3;

            li {
                margin: .5rem;
            }
        }
    }

    li {
        width: 100%;
        position: relative;
        margin-bottom: 1rem;
    }

    input {
        height: 5rem;
        outline: none;
        background-color: $azul1;
        margin: 0rem;
        border-color: transparent;
        color: $blanco2;
        font-size: 1.5rem;
        font-weight: $regular;
        padding: 1rem;
        width: 100%;
    }

    textarea {
        height: 10rem;
        outline: none;
        background-color: $azul1;
        margin: 0rem;
        border-color: transparent;
        color: $blanco2;
        font-size: 1.5rem;
        font-weight: $regular;
        width: 100%;
        padding: 1rem;

    }

    .li-submit {
        grid-column: 2 / 3;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 6rem;
        width: 100%;
        text-transform: uppercase;
        background-color: transparent;
        color: #fff;
        font-size: 1.8rem;
        overflow: hidden;
        transition: all .5s ease;
        margin: 2rem 0;
        font-weight: $bold;

        input {
            background-color: transparent;
            font-size: 1.8rem;
            height: 100%;
            font-weight: $bold;
            transition: all .35s cubic-bezier(0.69, 0.05, 0.34, 1);

            &:hover {
                color: #434343;
                cursor: pointer;
            }
        }

    }

}

.li-nombre,
.li-asunto,
.li-mensaje,
.li-email {
    label {
        &::before {
            content: "";
            height: 1px;
            width: 0%;
            background-color: $celeste;
            position: absolute;
            left: 0;
            bottom: 0;
            transition: all .3s ease-out;
        }

        // &:focus-within::before {
        //     width: 100%;
        // }

    }

    &:focus-within {
        label::before {
            width: 100%;
            box-shadow: 0 0 5px #00fff1, 0 0 10px #00fff1;
        }

    }
}