.header {
    background-color: transparent;

    .contenido-header {
        // position: fixed;
        padding: 1rem 2rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: row;
        width: 100%;

        @include tablet {
            padding: 1rem;
            position: inherit;
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-direction: row;
        }
    }

}

// menu de hamburguesa
.mobile-menu {
    z-index: 10;
    cursor: pointer;

    @include tablet {
        display: none;
    }

    img {
        width: 5rem;
    }
}



.navegacion-principal {
    transition: all .15s;
    visibility: visible;
    position: fixed;
    top: 0;
    right: 0;
    height: 100%;
    width: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translateX(20vw);
    background-color: darken($azul1, 3);
    z-index: 9;

    @include tablet {
        right: 0;
        position: inherit;
        height: auto;
        width: auto;
        background-color: transparent;
        margin: 1rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        visibility: visible;
        opacity: 1;
        transform: translateX(0vw);
    }

    ul {
        list-style: none;
        padding: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        row-gap: 2rem;
        width: 100%;

        li {
            padding: 1rem 0;
        }

        @include tablet {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;

            li {
                padding: 0;
                color: $naranja;
                position: relative;
                list-style: none;
                display: inline-block;
                margin: 1rem;
            }
        }
    }

    a {
        font-size: 2rem;
        color: #b0b2c3;
        padding: .5rem;
        transition: all .2s ease;

        &:hover {
            color: $blanco;
        }

        @include tablet {
            font-size: 1.6rem;

            &::before {
                content: "";
                height: 3px;
                // width: 0%;
                width: 100%;
                background-color: $naranja;
                position: absolute;
                left: 0;
                bottom: -3px;
                transition: transform .25s cubic-bezier(.694, .048, .335, 1);
                z-index: 2;

                transform: scaleX(0);
                transform-origin: right;
            }

            &:hover::before,
            &:hover::before {
                // width: 100%;
                transform: scaleX(1);
                transform-origin: left;
            }

            &::after {
                content: "";
                height: 3px;
                width: 100%;
                background-color: $celeste;
                position: absolute;
                left: 0;
                bottom: -3px;
                transition: transform .25s cubic-bezier(.694, .048, .335, 1);
                z-index: 1;

                transform: scaleX(0);
                transform-origin: right;
                transition-delay: .20s;
            }

            &:hover::after {
                transform: scaleX(1);
                transform-origin: left;
                transition-delay: .20s;
            }
        }
    }

    .cv {
        color: $celeste;
        transition: all .3s ease-out;

        &:hover {
            text-shadow: 0 0 5px #00fff1, 0 0 20px #00fff1;
            color: $celeste;
        }

        &::before {
            width: 0;
        }

        &::after {
            width: 0;
        }
    }
}

.mostrar {
    opacity: 1;
    width: 70%;
    pointer-events: auto;
    box-shadow: -10px 0px 30px -15px rgba(2, 12, 27, 0.7);
    transform: translateX(0vw);
}


.logo {
    transition: all .5s ease;
    position: relative;

    &:hover {
        box-shadow: 0 0 2px $naranja, 0 0 10px $naranja;
        cursor: pointer;

    }



    .logo-content {
        margin: 0;
        font-size: 2rem;
        padding: .7rem 1rem;
        border: 4px solid $blanco2;
        transition: all .4s ease;

        a {
            color: $blanco2;
            transition: all .3s ease;

        }

        &:hover {
            color: $naranja;
            border: 4px solid $naranja;
            text-shadow: 0 0 5px $naranja, 0 0 15px $naranja;

            a {
                color: $naranja;
            }
        }
    }
}

@keyframes stroke {
    from {
        stroke: #0d0d0d;
        stroke-width: 5;
        stroke-dasharray: 50 150;
        stroke-dashoffset: 350;
    }

    to {
        stroke: #0d0d0d;
        stroke-width: 5;
        stroke-dasharray: 50 150;
        stroke-dashoffset: -50;
    }
}