html {
    font-size: 62.5%;
    box-sizing: border-box;

    @include tablet {
        scroll-behavior: smooth;
    }
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    background: linear-gradient(to bottom, rgb(26, 38, 60) 0%, rgb(10, 19, 36) 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    background-attachment: fixed;
    background-size: cover;
    font-family: $fuentePrincipal;
    line-height: 1.3;
    width: 100%;
    min-height: 100%;
    overflow-x: hidden;
}

::selection {
    background: $azul1;
    color: $naranja;
}

body::-webkit-scrollbar {
    width: 8px;
    background: linear-gradient(to bottom, rgb(26, 38, 60) 0%, rgb(10, 19, 36) 100%);
    ;
}

body::-webkit-scrollbar-thumb {
    background: $naranja;
    border-radius: 10px;
}


.contenedor {
    max-width: 110rem;
    width: 90%;
    margin: 0 auto;
}

.contenedor-dentro {
    max-width: 90rem;
    width: 90%;
    margin: 0 auto;
}

p {
    font-size: 1.7rem;
    color: $blanco;
}

@include tablet {
    p {
        font-size: 2rem;
    }
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
}

h1,
h2,
h3 {
    margin: 0;
}

h1 {
    font-size: 4.4rem;
}

h2 {
    font-size: 4rem;
    color: $blanco;
}


h3 {
    font-size: 5rem;
    color: $blanco2;

    @include tablet {
        font-size: 7rem;
    }
}


.op0 {
    opacity: 0;
}