.aside {
    position: fixed;
    bottom: 1rem;
    right: 1rem;
    width: 5rem;
    height: 5rem;
    z-index: -1;

    @include tablet {
        bottom: 3rem;
        right: 3rem;
    }

    .go-top-button {
        width: 0rem;
        height: 0;
        background: rgba($naranja, 0.65);

        backdrop-filter: blur(10px);
        cursor: pointer;
        top: 50%;
        left: 50%;
        border-radius: 50%;
        transform: translate(-50%, -50%);
        transition: all .2s ease;
        box-shadow: 1px 1px 20px -10px rgba($azul1, 1);
        z-index: -1;

        &:hover {
            background: rgba($naranja, 0.8);
        }

        p {
            position: absolute;
            font-size: 4rem;
            font-weight: 800;
            top: -10px;
            left: 50%;
            transform: translate(-50%, -50%) scale(0);
            color: $blanco2;
            transition: all .2s ease;
            z-index: 10;
        }
    }

}

.show {
    z-index: 10;
}

.show {
    .go-top-button {
        animation: popup .4s ease;
        width: 6rem;
        height: 6rem;
        z-index: 11;

        p {
            transform: translate(-50%, -50%)scale(1);
        }
    }

}

@keyframes popup {
    0% {
        width: 0;
        height: 0%;
    }

    50% {
        width: 8rem;
        height: 8rem;
    }

    100% {
        width: 6rem;
        height: 6rem;
    }
}