.transition {
  position: absolute;
  height: 100%;
  width: 30%;
  background: #d6d6d6;
  transform: skewX(-5deg) translateX(-50px);
  transition: 2s all ease-in-out;
  -webkit-transition: 2s all ease-in-out;
}

.contenido {
  position: relative;
  padding: 200px 0 0 200px;
  color: #000;
  z-index: 10;
  height: 300px;
}

.cta {
  outline: none;
  border: none;
  text-decoration: none;
  text-transform: uppercase;
  background: #202020;
  color: #eaeaea;
  box-sizing: border-box;
  margin-top: 20px;
  padding: 10px 40px;
}

.anim-trans {
  animation: anim 4s ease-in-out;
}

.blur {
  overflow: hidden;
}

.blur-child {
  // -webkit-filter: blur(5px) brightness(0.7);
  filter: blur(5px) brightness(0.7);
  // -webkit-transition: all .3s ease;
  pointer-events: none;
  // -webkit-user-select: none;
  // -moz-user-select: none;
  // -ms-user-select: none;
  user-select: none;
}

.toasti-error {
  font-size: 1.7rem;
  background: linear-gradient(to right, #fb8e5b, #ff793f) !important;

}

.toasti-success {
  font-size: 1.7rem;
  color: #434343 !important;
  background: linear-gradient(to right, #20e2d7, #20e2d7) !important;
}

@keyframes anim {
  0% {}

  20% {
    z-index: 11;
    transform: skewX(5deg) translateX(-100%);
  }

  40% {
    transform: skewX(0deg) translateX(0);
    width: 100%;
    z-index: 11;
    box-shadow: 10px 10px 5px #eaeaea;
  }

  60% {
    transform: skewX(3deg) translateX(0);
    width: 100%;
    z-index: 11;
    box-shadow: 10px 10px 5px #eaeaea;
  }

  80% {
    transform: skewX(1deg) translateX(-100%);
    width: 60%;
    z-index: 11;
    box-shadow: 10px 10px 5px #eaeaea;
  }

  100% {
    transform: skewX(-5deg) translateX(-50px);
    width: 30%;
    z-index: 1;
    box-shadow: none;
  }
}


@keyframes toUp {
  from {
    transform: translateY(19px);
    opacity: 0;
  }

  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes toUpUp {
  from {
    transform: translateY(100px);
    opacity: 0;
  }

  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes toDown {
  from {
    transform: translateY(-19px);
    opacity: 0;
  }

  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes neon {
  0% {
    text-shadow: 0 0 6px #00fff1;
  }

  50% {
    text-shadow: 0 0 5px #00fff1, 0 0 10px #00fff1;
  }

  100% {
    text-shadow: 0 0 6px #00fff1;
  }
}