#skills {
    margin: 5rem auto;
    padding: 5rem 0;

    h2 {
        text-align: center;
        margin-bottom: 5rem;
    }

    ul {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        row-gap: 4rem;
        padding: 0;

        @include tablet {
            grid-template-columns: repeat(4, 1fr);
        }
    }

    p {
        text-align: center;
    }

    svg {
        width: 10rem;
        height: 10rem;
        transform: scale(0.95);
        transition: all .7s ease;
    }

    .skills-label {
        font-size: 1.5rem;
        text-align: center;
        color: $blanco;
        margin-top: 1rem;
        position: absolute;
        bottom: -25px;
        left: 0;
        right: 0;
        transform: translateY(1rem);
        transition: transform .3s;
        opacity: 0;
        transition: opacity .3s, -webkit-transform .3s cubic-bezier(.694, .048, .335, 1);
        transition: opacity .3s, transform .3s cubic-bezier(.694, .048, .335, 1);
        transition: opacity .3s, transform .3s cubic-bezier(.694, .048, .335, 1), -webkit-transform .3s cubic-bezier(.694, .048, .335, 1);
    }

    .vector {
        transition: all .3s;
    }

}

#skills li {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    position: relative;

    &:hover {
        svg {
            transform: scale(1);
            transition: all .7s ease;
            // transform: scaleZ(1);
        }

        .skills-label {
            transform: translateY(0);
            opacity: 1;
        }

        #html .html-color {
            fill: #FF5722;
        }

        #css .css-color {
            fill: #2196f3;
        }

        #js .js-color {
            fill: #f7df1e;
        }

        #ts {
            .ts-color {
                fill: #007ACC
            }
        }

        #react .react-color {
            fill: #00d8ff;
        }

        #sass .sass-color {
            fill: #CD6799;
        }

        #php .php-color {
            fill: #777bb3;
        }

        #mysql {
            .mysql-blue {
                fill: #00678F;
            }

            .mysql-orange {
                fill: #E38D1A;
            }
        }
    }
}