.sobre-mi {
    padding: 10rem 0 10rem 0;


    @include tablet {
        h2::after {
            content: "";
            display: block;
            position: relative;
            top: -2.5rem;
            right: -18rem;
            width: 20rem;
            height: 2px;
            background-color: $azul1;
            margin-left: 2rem;
        }
    }

    ul.ul {
        color: $blanco2;
        font-size: 2rem;

        li::marker {
            color: $naranja;
        }
    }
}

.contenido-sobre-mi {
    line-height: 1.5;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    @include tablet {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        column-gap: 5rem;
    }

    .descripcion {
        grid-column: 1/5;

        p {
            color: #c2c3d0;
        }

        ul {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
        }

        li {
            color: #c2c3d0;
            font-size: 1.7rem;

        }

        @include tablet {
            li {
                font-size: 2rem;
            }
        }
    }

    .imagen-sobre-mi {
        position: relative;
        max-width: 300px;
        &::before {
            content: "";
            position: absolute;
            width: 100%;
            height: 98.9%;
            top: 0;
            left: 0;
            background-color: rgba($celeste, .3);
            opacity: 1;
            transition: all .2s;
            z-index: 10;
        }

        &:hover {
            &::before {
                opacity: 0;
            }
        }
        
    }

    img {
        border-radius: .2rem;
        height: 100%;
        max-width: 300px;
        
    }

    @include tablet {
        img {
            max-width: 300px;
        }
    }


}