.btn {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 6rem;
    width: 20rem;
    border-radius: 4px;
    text-transform: uppercase;
    background-color: transparent;
    color: #fff;
    font-size: 1.8rem;
    overflow: hidden;
    transition: all .35s cubic-bezier(0.69, 0.05, 0.34, 1);
    border: 1px solid $celeste;
    // margin-bottom: 4rem;
    z-index: 0;
    font-weight: $bold;

    &::before {
        content: "";
        position: absolute;
        left: 0;
        right: -10;
        top: 0;
        margin: auto;
        background-color: $celeste;
        transition: all .35s ease;
        z-index: -1;


    }

    &--1::before {
        width: 100%;
        height: 100%;
        transform-origin: left;
        transform: scaleX(0);
        transition: transform .35s ease;
    }

    &--1:hover {
        color: #434343;

        &::before {
            width: 100%;
            // transform-origin: left;
            transform: scaleX(1);

        }
    }

    &--2:hover {
        background-color: #20e2d7;
        box-shadow: 0 0 10px #20e2d7, 0 0 20px #20e2d7, 0 0 20px #20e2d7;
        color: #434343;
    }

    &--3:hover {
        animation: shadow-pulse 1000ms 1;
        background-color: transparent;
        color: #434343
    }

    &--4::before {
        left: -520px;
        right: 0;
        width: 100%;
        height: 150%;
        transition: all .75s ease;
        transform: rotate(45deg);
        box-shadow: 0 0 10px #20e2d7, 0 0 20px #20e2d7, 0 0 20px #20e2d7;
    }

    &--4:hover::before {
        left: 520px;

    }

}

@keyframes shadow-pulse {
    0% {
        box-shadow: 0 0 0 0 rgba(32, 226, 215, 0.6);
    }

    100% {
        box-shadow: 0 0 8px 16px rgba(32, 226, 215, 0);
    }
}